import React, { Component, PropTypes } from 'react';

//导入样式 start
import './proclaimDetail.scss'
//导入样式 end
import Backbar from '../../module/backbar2/backbar2';



class ProclaimList extends Component{
    constructor(){
        super();
    }
    componentWillMount(){
        //在此处初始化状态
        this.state={
            adTitle: '',
            adDetailUrl:'',
            adSignlePictureUrl: '',
            adDetailType: ''
        }
    }

    componentDidMount(){
        var that = this;

        // 存储 start
        componentStore.set(this);
        // 存储 end



        Utils.requestData({
            url: config.api +"qjlianlian/front/v1/ad/getHeadDetail",
            method: 'post',
            data: {
                "adId":Utils.Url.parseUrl(location.href).params.adId
            },
            callback: function(data){
                if(data.resultCode == 0){
                    componentStore.update(that,{
                        adTitle: data.data.adInfoDetail.adTitle,
                        adDetailUrl: data.data.adInfoDetail.adDetailUrl,
                        adSignlePictureUrl: data.data.adInfoDetail.adSignlePictureUrl,
                        adDetailType: data.data.adInfoDetail.adDetailType
                    })



                }
            }
        });


    };
    componentWillUnmount(){
        // 清除 start
        componentStore.clear(this);
        // 清除 end
    };

    render(){
        var that = this;

        return (
            <div className="proclaimDetail">
                <Backbar $id="messageCenter">
                    {that.state.adTitle}
                </Backbar>

                {(function () {

                    if(
                        that.state.adDetailType==4
                    ){
                        return (
                            <iframe id="iframepage" style={{width:'100%'}} onLoad={()=>{
                                var ifm= document.getElementById("iframepage");
                                ifm.height=document.documentElement.clientHeight;
                            }} frameborder="0" src={that.state.adSignlePictureUrl} frameborder="0"></iframe>
                        )


                    }else{

                        return (
                            <img src={that.state.adDetailUrl} alt=""/>
                        )
                    }


                })()}



            </div>
        )
    }

}

export default ProclaimList;



